.main-container {
    position: fixed;
    width: 90vw;
    height: 90vh;
    border: 1px grey solid;
    top: 5vh;
    left: 5vw;
    overflow: auto;
    
}

.main-subcontainer{
    width: 100%;
    height: 100%;
    position: relative;
    margin: 0px;
    padding: 0px;

}



